import React, { useState } from "react";
import { Box, Text, SimpleGrid, HStack, Button } from "@chakra-ui/react";
import homeUsers from "@/data/homeUsers";
import DateRangePicker from "@/common/DateRangePicker";
import useColorModeStyles from "@/utils/useColorModeStyles";
import UserCard from "../user/UserCard";
import UserCardSketelon from "../user/UserCardSketelon";
import UserCardContainer from "../user/UserCardContainer";

const MainInstagram = () => {
  const { bg, textColor, borderColor } = useColorModeStyles();
  const [startDate, setStartDate] = useState<string>("");
  const [endDate, setEndDate] = useState<string>(new Date().toISOString().split("T")[0]);
  const [page, setPage] = useState(1);
  const pageSize = 8; // Number of users per page

  const paginatedUsers = homeUsers.slice((page - 1) * pageSize, page * pageSize);
  const isLoading = false; // Placeholder for real loading state

  const handleNextPage = () => {
    if (paginatedUsers.length === pageSize) {
      setPage(page + 1);
    }
  };

  const handlePreviousPage = () => {
    if (page > 1) {
      setPage(page - 1);
    }
  };

  return (
    <Box p={1}>
      {/* Title Section */}
      <Box
        border="1px solid"
        borderColor={borderColor}
        borderRadius="md"
        p={1}
        mb={1}
        bg={bg}
      >
        <Text fontSize="3xl" fontWeight="bold" color={textColor}>
          Instagram Users
        </Text>
      </Box>

      {/* Date Range Picker */}
      <Box
        border="1px solid"
        borderColor={borderColor}
        borderRadius="md"
        p={1}
        mb={4}
        bg={bg}
      >
        <DateRangePicker setStartDate={setStartDate} setEndDate={setEndDate} />
      </Box>

      {/* User Cards Grid */}
      <SimpleGrid columns={{ sm: 1, md: 2, lg: 3 }} spacing={6} padding="10px">
        {isLoading
          ? Array.from({ length: pageSize }).map((_, index) => (
              <UserCardContainer key={index}>
                <UserCardSketelon />
              </UserCardContainer>
            ))
          : paginatedUsers.map((user) => (
              <UserCardContainer key={user.id}>
                <UserCard user={user} />
              </UserCardContainer>
            ))}
      </SimpleGrid>

      {/* Pagination Controls */}
      <HStack justify="center" marginTop={4}>
        <Button onClick={handlePreviousPage} isDisabled={page === 1}>
          Previous
        </Button>
        <Button onClick={handleNextPage} isDisabled={paginatedUsers.length < pageSize}>
          Next
        </Button>
      </HStack>
    </Box>
  );
};

export default MainInstagram;
